        </el-form-item>
        <h4 v-if="this.group === true">éé¿èµæç¡®è®¤</h4>
        <el-form-item label="é¢ç³»" prop="faculty">
          <el-select v-model="ruleForm.faculty" placeholder="éæ©é¢ç³»">
            <el-option label="è®¡ç®æº" value="CS"></el-option>
            <el-option label="èªå¨å" value="AUTO"></el-option>
            <el-option label="çµå­" value="EE"></el-option>
