            <el-option label="ç·" value="M"></el-option>
            <el-option label="å¥³" value="F"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="è¯ä»¶å·ç " prop="id_card">
          <el-input v-model="ruleForm.id_card"></el-input>
        </el-form-item>
        <el-form-item label="å­¦å·" prop="student_id">
