          { legth: 10, message: 'é¿åº¦10ä¸ªå­ç¬¦', trigger: 'blur' }
        ],
        clothes_size: [
          { required: true, message: 'è¯·éæ©è¡£æå·ç ', trigger: 'change' }
        ],
        cellphone_num: [
          { required: true, message: 'è¯·è¾å¥å­¦å·', trigger: 'blur' },
          { min: 11, max: 11, message: 'é¿åº¦12ä¸ªå­ç¬¦', trigger: 'blur' }
        ]
