  },
  methods: {
    user_info_request (uid) {
      this.$http.get('http://111.230.226.45:8888/api/user_info_request/' + uid).then((response) => {
        var res = JSON.parse(response.bodyText)
        if (res.error_num === 0) {
          this.ruleForm = res.list[0].fields
          this.user_pk = res.list[0].pk
          this.ruleForm.birth_date = this.ruleForm.birth_date + 'T00:00:00.000Z'
        } else {
          this.$message.error('è·åé¡¹ç®åè¡¨å¤±è´¥"')
          console.log(res['msg'])
        }
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.registerForm.user_id = this.uid
          this.registerForm.project_id = this.pid
          if (this.group === false) {
            this.$http.post('http://111.230.226.45:8888/api/project_register/' + this.uid + '/' + this.pid).then((response) => {
              var res = JSON.parse(response.bodyText)
              if (res.error_num === 0) {
                this.$message({message: 'æ¥åæå', type: 'success'})
              }
              if (res.error_num === 1) {
                this.$message({message: 'æ±æ­ï¼åºéäºï¼', type: 'warning'})
              }
              if (res.error_num === 2) {
                this.$message({message: 'é¡¹ç®æä¸ªäººèµæåºé', type: 'warning'})
              }
              if (res.error_num === 3) {
                this.$message({message: 'å·²è¾¾æé«æ¥åäººæ°', type: 'warning'})
              }
              if (res.error_num === 4) {
                this.$message({message: 'æ¨å·²å¨æ¥ååè¡¨é', type: 'warning'})
              }
            })
            this.dialogFormVisible = false
            this.closeDialog()
          } else {
            this.groupForm.group_name = this.ruleForm.teamname
            this.$http.post('http://111.230.226.45:8888/api/add_group/' + this.uid + '/' + this.pid, this.groupForm, {emulateJSON: true}).then((response) => {
              this.dialogFormVisible = false
              var res = JSON.parse(response.bodyText)
              if (res.error_num === 0) {
                this.$message({message: 'åå»ºæå', type: 'success'})
                this.$emit('finish')
              }
              if (res.error_num === 1) {
                this.$message({message: 'æ±æ­ï¼åºéäºï¼', type: 'warning'})
                this.$emit('error')
              }
              if (res.error_num === 2) {
                this.$message({message: 'é¡¹ç®æä¸ªäººèµæåºé', type: 'warning'})
                this.$emit('error')
              }
              if (res.error_num === 3) {
                this.$message({message: 'å·²è¾¾æé«æ¥åäººæ°', type: 'warning'})
                this.$emit('error')
              }
              if (res.error_num === 4) {
                this.$message({message: 'æ¨å·²å¨æ¥ååè¡¨é', type: 'warning'})
                this.$emit('error')
              }
            })
